import Map from '../../assets/map.png'
import './add-product.css'
import '../../index.css'
import FileImg from '../../assets/file.svg'

function Addads() {
    return (
        <div className='productWrapper container'>
            <p className='imputTitle'>Цена</p>
            <div className='productStyle'>
                <div>
                    <input className='bradius' type={'number'}></input>
                </div>

                <select className='bradius'>
                    <option>Сум</option>
                    <option>Доллар</option>
                    <option>Рубль</option>
                </select>

                <div className='productStyleCheck'>
                    <input type={'checkbox'}></input>
                    <p className='checkTitle' style={{margin: '0'}}>Договорная</p>
                </div>
            </div>

            <p className='imputTitle'>Количество комнат*</p>
            <div className='productStyle'>
                <div>
                    <input  className='bradius' type={'number'}></input>
                </div>

                <input  className='bradius' type={'number'}></input>

                <input  className='bradius' type={'number'}></input>
            </div>

            <p className='imputTitle'>Расположение*</p>
            <div>
                <div className='productStyle'>
                    <div>
                        <div className='productStyleCheck'>
                            <input type={'checkbox'}></input>
                            <p className='checkTitle'>В городе</p>
                        </div>
                    </div>

                    <div className='productStyleCheck'  style={{marginLeft: '88px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Вдоль трассы</p>
                    </div>

                    <div className='productStyleCheck' style={{marginLeft: '52px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>В дачном массиве</p>
                    </div>
                </div>

                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>В пригороде</p>
                    </div>

                    <div className='productStyleCheck' style={{marginLeft: '64px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Возле водоема, реки</p>
                    </div>

                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>На закрытой территории</p>
                    </div>
                </div>

                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>В сельской местности</p>
                    </div>

                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>В предгорьях</p>
                    </div>
                </div>
            </div>

            <div className='productStyle'>
                <div>
                    <p className='imputTitle'>Этажность дома*</p>
                    <input  className='bradius' type={'number'}></input>
                </div>

                <div>
                    <p className='imputTitle'>Высота потолков</p>
                    <input  className='bradius' type={'number'}></input>
                </div>

                <div>
                    <p className='imputTitle'>Площадь участка*</p>
                    <input  className='bradius' type={'number'}></input>
                </div>
            </div>

            <p className='imputTitle'>Состояние дома</p>
            <div>
                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Авторский проект</p>
                    </div>

                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Требует ремонта</p>
                    </div>

                    <div className='productStyleCheck'  style={{marginLeft: '41px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Черновая отделка</p>
                    </div>
                </div>

                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Средний ремонт</p>
                    </div>

                    <div className='productStyleCheck'  style={{marginLeft: '10px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Предчистовая отделка</p>
                    </div>

                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Под снос</p>
                    </div>
                </div>

                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Евроремонт</p>
                    </div>

                    <div className='productStyleCheck'  style={{marginLeft: '43px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Не достроен</p>
                    </div>
                </div>
            </div>

            <div className='productStyle'>
                <div>
                    <p className='imputTitle'>Тип дома</p>

                    <select className='bradius'>
                        <option className='optionSty'>Коттедж</option>
                        <option>2</option>
                        <option>3</option>
                    </select>
                </div>

                <div>
                    <p className='imputTitle'>Электричество</p>

                    <select className='bradius'>
                        <option className='optionSty'>Есть возможность подключения</option>
                        <option>2</option>
                        <option>3</option>
                    </select>
                </div>
            </div>

            <p className='imputTitle'>Отопление</p>
            <div>
                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Центральное</p>
                    </div>

                    <div className='productStyleCheck' style={{marginLeft: '49px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>На жидком топлив</p>
                    </div>

                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Без отопления</p>
                    </div>
                </div>

                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>На газе</p>
                    </div>

                    <div className='productStyleCheck'  style={{marginLeft: '89px'}}>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Электрическое</p>
                    </div>
                </div>

                <div className='productStyle'>
                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>На твердом топливе</p>
                    </div>

                    <div className='productStyleCheck'>
                        <input type={'checkbox'}></input>
                        <p className='checkTitle'>Смешанное</p>
                    </div>
                </div>
            </div>

            <div>
                <p className='imputTitle'>Электричество</p>

                <p className='mainTitle'>Электричество</p>

                <textarea className='bradius' rows={'10'} cols={'85'} type={'text'}></textarea>

                <p className='imputTitle' style={{mixBlandMode: 'normal', opacity: '0.55'}}>9000 знаков осталос</p>
            </div>

            <div className='fileWrapper'>
                <input id='file1' className='file' type={'file'}></input>
                <input id='file2' className='file' type={'file'}></input>
                <input id='file3' className='file' type={'file'}></input>
                <input id='file4' className='file' type={'file'}></input>
                <input id='file5' className='file' type={'file'}></input>
                <input id='file6' className='file' type={'file'}></input>
                <input id='file7' className='file' type={'file'}></input>
                <input id='file8' className='file' type={'file'}></input>
                <input id='file9' className='file' type={'file'}></input>
                <input id='file10' className='file' type={'file'}></input>
                
                <label htmlFor={'file1'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file2'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file3'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file4'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file5'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file6'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file7'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file8'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file9'}>
                    <img src={FileImg} alt='erkin'/>
                </label>

                <label htmlFor={'file10'}>
                    <img src={FileImg} alt='erkin'/>
                </label>
            </div>

            <div>
                <h3 className='mainTitle'>Местоположение объекта</h3>

                <div className='productStyle'>
                    <div>
                        <p className='imputTitle' style={{marginLeft: '5px'}}>Регион</p>

                        <select className='bradius regSelect'>
                            <option className='optionSty'>Ташкент</option>
                            <option>2</option>
                            <option>3</option>
                        </select>
                    </div>

                    <div>

                        <p className='imputTitle'  style={{marginLeft: '34px'}}>Город / Район</p>
                        <select className='bradius raySelect'>
                            <option className='optionSty'>Мирза Улугбекский район</option>
                            <option>2</option>
                            <option>3</option>
                        </select>
                    </div>
                </div>

                <img src={Map} alt='erkin'></img>
            </div>

            <div className='prContact'>
                <div>
                    <p className='prContactText imputTitle'>Телефонный номер</p>

                    <input className='bradius prContactInp' type={'number'}></input>
                </div>

                <div>
                    <p className='prContactText imputTitle'>Email-адрес</p>

                    <input className='bradius prContactInp' type={'number'}></input>
                </div>

                <div>
                    <p className='prContactText imputTitle'>Контактный лицо</p>

                    <input className='bradius prContactInp' type={'number'}></input>
                </div>
            </div>

            <div className='productStyle'>
                <button className='addProductBtn mainTitle'>Добавить обьявления бесплатно</button>

                <p className='linkText mainTitle'>Предпросмотр</p>
            </div>
        </div>
    )
}

export default Addads